<template>
  <div class="card">
    <div class="card-content">
      <p v-if="content.name" class="title">{{content.name}}</p>
      <p v-if="content.subtitle" class="subtitle" v-html="content.subtitle"></p>
      <div v-if="content.description" v-html="content.description" class="content">
      </div>
    </div>

    <footer v-if="content.url" class="card-footer">
      <a v-on:click="click(content.url)" class="card-footer-item button is-primary">Link to {{strippedLink}}</a>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'card',
  data() {
    return {
    };
  },
  props: ['content'],
  computed: {
    /**
     * Strips https:// from the link
     */
    strippedLink() {
      if (this.content.url == null) {
        return '';
      }

      const re = new RegExp('https?://');
      return this.content.url.replace(re, '');
    },
  },
  methods: {
    click(url) {
      this.$ua.trackEvent('Link', url);
      window.open(url);
    },
  },
};
</script>

