<template>
  <div>
    <section class="section dark-background">
      <div class="container">
        <div class="heading">
          <h1 class="title">Skills</h1>
        </div>

        <div class="card-container">
          <skill-card v-for="skillGroup in skillGroups" :key="skillGroup.name" :content="skillGroup" class="card-item">
          </skill-card>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import axios from 'axios';
import SkillCard from './SkillCard';

export default {
  name: 'skills',
  data() {
    return {
      skillsUrl: 'data/skills.json',
      skillGroups: [],
    };
  },
  components: {
    SkillCard,
  },
  created() {
    // Fetch the data
    axios.get(this.skillsUrl)
      .then((resp) => {
        if (resp.status === 200) {
          this.skillGroups = resp.data;
        }
      })
      .catch((err) => {
        // eslint-disable-next-line
        console.log(err);
      });
  },
};
</script>

