<template>
  <div>
    <section class="section dark-background">
      <div class="container">
        <div class="heading">
          <h1 class="title">Employment</h1>
        </div>

        <div class="card-container">
          <card v-for="job in jobs" :key="job.name" :content="job" class="card-item">
          </card>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import axios from 'axios';
import Card from './Card';

export default {
  name: 'employment',
  data() {
    return {
      jobsUrl: 'data/jobs.json',
      jobs: [],
    };
  },
  components: {
    Card,
  },
  created() {
    // Fetch the data
    axios.get(this.jobsUrl)
      .then((resp) => {
        if (resp.status === 200) {
          this.jobs = resp.data;
        }
      })
      .catch((err) => {
        // eslint-disable-next-line
        console.log(err);
      });
  },
};
</script>

