<template>
  <div>
    <section class="section light-background">
      <div class="container">
        <div class="heading">
          <h1 class="title">Projects</h1>
        </div>

        <div class="card-container">
          <card v-for="project in projects" :key="project.name" :content="project" class="card-item">
          </card>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import axios from 'axios';
import Card from './Card';

export default {
  name: 'projects',
  data() {
    return {
      projectsUrl: 'data/projects.json',
      projects: [],
    };
  },
  components: {
    Card,
  },
  created() {
    // Fetch the data
    axios.get(this.projectsUrl)
      .then((resp) => {
        if (resp.status === 200) {
          this.projects = resp.data;
        }
      })
      .catch((err) => {
        // eslint-disable-next-line
        console.log(err);
      });
  },
};
</script>

