<template>
  <div>
    <section class="section light-background">
      <div class="container">
        <div class="heading">
          <h1 class="title">Education</h1>
        </div>

        <div class="card-container">
          <card v-for="education in educations" :key="education.name" :content="education" class="card-item">
          </card>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import axios from 'axios';
import Card from './Card';

export default {
  name: 'education',
  data() {
    return {
      educationUrl: 'data/education.json',
      educations: [],
    };
  },
  components: {
    Card,
  },
  created() {
    // Fetch the data
    axios.get(this.educationUrl)
      .then((resp) => {
        if (resp.status === 200) {
          this.educations = resp.data;
        }
      })
      .catch((err) => {
        // eslint-disable-next-line
        console.log(err);
      });
  },
};
</script>

